.BoardBottomDiv {
  border: 2px solid rgb(220, 220, 220);
  height: 200px;
  width: 440px;
  margin: 0 auto;
  margin-top: 10px;
}

.BoardBottomDiv h3{
  font-family: Verdana, sans-serif;
  padding: 10px;
  margin: 0 auto;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  color: white;
  width: 420px;
}

#P1Turn {
  background-color: rgb(255, 120, 111);
}

#P2Turn {
  background-color: rgb(96, 132, 194);
}

.CardStack {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  /*padding: 30px;*/
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.GameCard {
  border: 2px solid rgb(220, 220, 220);
  height: 130px;
  width: 78px;
  margin-top: 15px;
  /*padding-top: 30px;*/
}

.GameCard:hover {
  cursor: pointer;
  -webkit-box-shadow: 5px 5px 20px rgb(160, 160, 160);
          box-shadow: 5px 5px 20px rgb(160, 160, 160);
  /*height: 130px;
  width: 78px;*/
  margin-top: 5px;
}

